import axios from 'axios'
import tools from '@/common/tools.js'

export default {
  name: 'coupon',
  data() {
    return {
      type: 0,
      couponData: [],
      allData: {},
      totalPage: 1,
      loadingShow: false,
      noDataShow: false,
      noDataPopShow: false,
      nowPage: 1,
      busy: true
    }
  },
  activated() {
    this.resetData()
    this._getCouponData()
  },
  methods: {
    loadMore() {
      console.log('重复加载', this.busy)
      this.busy = true
      this.loadingShow = true
      // 官方示例中延迟了1秒，防止滚动条滚动时的频繁请求数据
      this.timer = setTimeout(() => {
        // 这里请求接口去拿数据，实际应该是调用一个请求数据的方法
        this.nowPage++
        this._getCouponData()
      }, 1000)
    },
    changeType(num) {
      if (this.type == num) {
        return false
      }
      this.resetData()
      this.type = num
      this._getCouponData()
    },
    resetData() {
      this.totalPage = 0
      this.nowPage = 1
      this.busy = true
      this.couponData = []
      this.allData = {}
      this.loadingShow = false
      this.noDataShow = false
      this.noDataPopShow = false
    },
    _getCouponData() {
      const url = '/Mobile/User/coupon.html'
      axios.get(url, {
        params: {
          type: this.type,
          page: this.nowPage
        }
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            this.totalPage = res.data.totalPages
            this.allData = res.data
            this.couponData = [...this.couponData, ...res.data.coupon_list]
            if (this.couponData.length > 0) {
              this.noDataPopShow = false
            } else {
              this.noDataPopShow = true
            }
            console.log('页数', this.nowPage, this.totalPage)
            if (this.nowPage >= this.totalPage) {
              this.busy = true
              this.noDataShow = true
              this.loadingShow = false
            } else {
              this.busy = false
            }
          } else {
            tools.showError(res.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}



